"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";

const AnimeDetail = () => {
  const router = useRouter();
  const { id } = router.query;

  const [anime, setAnime] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  useEffect(() => {
    async function loadAnime() {
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}`);
        const json = await res.json();
        setAnime(json.data);
      } catch {
        setError(true);
      } finally {
        setLoading(false);
      }
    }

    if (id) {
      loadAnime();
    }
  }, [id]);

  if (loading) return <p style={{ color: "white", textAlign: "center" }}>Carregando...</p>;
  if (error || !anime) return <p style={{ color: "white", textAlign: "center" }}>Erro ao carregar anime.</p>;

  return (
    <div
      style={{
        width: "100%",
        minHeight: "100vh",
        backgroundImage: "url('/scenario.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        padding: 0,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        margin: 0,
      }}
    >
      <h1 style={{ color: "white", marginBottom: 20 }}>{anime.title}</h1>
      <div style={{ display: "flex", gap: 20 }}>
        <img
          src={anime.images?.jpg?.image_url}
          alt={anime.title}
          width={150}
          height={200}
          style={{ objectFit: "cover", borderRadius: 6 }}
        />
        <div style={{ color: "white" }}>
          <p>Ano: {anime.year || "Desconhecido"}</p>
          <p>Score: {anime.score || "N/A"}</p>
          <p>Status: {anime.status || "N/A"}</p>
        </div>
      </div>
    </div>
  );
};

export default AnimeDetail;
